import React, { useState, useEffect } from 'react';
import Sivu from './components/Sivu';
import Tulot from './components/Tulot';
import Meno from './components/Meno';
import {Container, Typography } from '@mui/material';
import Menot from './components/Menot';
import Piirakka from './components/Piirakka';



const App: React.FC = (): React.ReactElement =>  {

  const [tulot, setTulot] = useState<number>(0);
  const [menot, setMenot] = useState<Meno[]>([]);
  const [menotYhteensa, setMenotYhteensa] = useState<number>(0);

  useEffect(() => {
    const summa = menot.reduce((total, meno) => total + meno.summa, 0);
    setMenotYhteensa(Number(summa.toFixed(2)));
  }, [menot]);


  return (

    <Container maxWidth = "sm" sx={{
      height:"100vh",
      display:"flex",
      flexDirection:"column",
      minHeight:"100vh"
    }}>

      <Typography variant="h4">
          Budjettilaskuri
      </Typography>

      <Sivu>
    
        <Typography 
          variant="h5"
          sx={{marginBottom: "20px"}}
        >
          Tulot
        </Typography>

        <Tulot tulot={tulot} setTulot={setTulot} />

        {tulot 
          ? <Typography 
              variant="body1" 
              sx={{marginTop: "10px"}}
            > 
              Tulosi ovat:  {tulot} 
            </Typography> 
          : <></>
        }
        
      </Sivu>

      <Sivu>

        <Typography 
          variant="h5"
          sx={{marginBottom: "20px"}}
        >
          Menot
        </Typography>

        <Menot 
          menot={menot} 
          setMenot={setMenot}
        />
        
        {menotYhteensa !== 0
          ? <>
            <Typography 
              variant="body1"  
              sx={{marginTop: "10px"}}
            >
                Menosi:
            </Typography>

            <ul>
                {menot.map((meno : Meno, idx :  number) => (
                    <li key={idx}>
                         {meno.summa} - {meno.tyyppi}
                    </li>
                ))}
            </ul>

            <Typography variant='body1'>
              Yhteens:  {menotYhteensa}
            </Typography>
            </>
          : <></>
        }

      </Sivu>

      {tulot !== 0 && menotYhteensa !== 0
        ? <Piirakka 
            tulot={tulot} 
            menot={menot}
            menotYhteensa={menotYhteensa}
          />
          : null
      }

    </Container>
  );
}

export default App;