import { Button, FormControl, InputAdornment, InputLabel, MenuItem, Select, TextField } from '@mui/material';
import { useState } from 'react';
import Meno from './Meno';

interface Props {
    menot : Meno[];
    setMenot : (menot : Meno[]) => void;
}

const Menot : React.FC<Props> = (props : Props) : React.ReactElement => {

    const [menoApu, setMenoApu] = useState<Meno>({
                                                    summa: 0, 
                                                    tyyppi: ''
                                                });


    const lisaaMeno = () => {
        if (menoApu.summa > 0 && menoApu.tyyppi) {  // Tarkistetaan, ett menoApu on kelvollinen
            props.setMenot([...props.menot, menoApu]);  
            setMenoApu({ summa: 0, tyyppi: '' }); 
        } else {
            alert('Tyt sek meno ett menon tyyppi!');
        }
    };

    return (

        <div 
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
            }}
        >

           <TextField
                sx={{
                    marginRight: "10px",
                    width: "120%"
                    }}
                label = "Meno"
                placeholder="syt meno"
                type='number'
                variant = "outlined"
                value = {menoApu.summa === 0 
                        ? ""
                        : menoApu.summa
                    }
                slotProps={{
                    input : {
                    endAdornment: <InputAdornment position="end"></InputAdornment>
                },
                }}
                onChange={( e : any) => 
                    setMenoApu({
                        ...menoApu,
                        summa: Number(e.target.value)
                    })
                }
            />

             <FormControl fullWidth variant="outlined">
             <InputLabel id="menoTyyppi">Tyyppi</InputLabel>
                <Select
                    label="Tyyppi"
                    labelId="tyyppi-label"
                    value={menoApu.tyyppi}
                    onChange={(e: any) =>
                        setMenoApu({
                            ...menoApu,
                            tyyppi: e.target.value
                        })
                    }
                >
                    <MenuItem value="Asuminen">Asuminen</MenuItem>
                    <MenuItem value="Ruoka">Ruoka</MenuItem>
                    <MenuItem value="Viihde">Viihde</MenuItem>
                    <MenuItem value="Liikenne">Liikenne</MenuItem>
                    <MenuItem value="Muu ">Muu</MenuItem>
                </Select>
                </FormControl>

            

            <Button
                sx={{
                    marginLeft: "10px",
                    width: "200px",
                    padding: "10px"
                    }}
                size="medium"
                variant = "contained"
                onClick = {lisaaMeno}
            >
                Lis meno
            </Button>


        </div>

    );

}

export default Menot;