import { Button, InputAdornment, TextField } from '@mui/material';
import { useState } from 'react';

interface Props {
    tulot : number;
    setTulot : (tulot : number) => void;
}

const Tulot : React.FC<Props> = (props : Props) : React.ReactElement => {

    const [tuloApu, setTuloApu] = useState<number>(0);

    return (

        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
            }}
        >

            <TextField
                label = "Tulot"
                placeholder='Syt tulot'
                type='number'
                variant = "outlined"
                value = {tuloApu === 0 
                    ? '' 
                    : tuloApu
                }
                slotProps={{
                    input : {
                    endAdornment: <InputAdornment position="end"></InputAdornment>
                },
                }}
                onChange={( e : any ) => setTuloApu(Number(e.target.value))} 
            />
            
            <Button
                sx={{
                    marginLeft: "10px"
                }}
                size="large"
                variant = "contained"
                onClick = {() => {
                    props.setTulot(tuloApu)
                    setTuloApu(0)
                }}
            >
                Tallenna
            </Button>

        </div>

    );

}

export default Tulot;