interface Props {
    children : React.ReactElement | React.ReactElement[] | string | boolean | undefined;
}

const Sivu : React.FC<Props> = (props : Props) : React.ReactElement => {

    return (

        <div style={{
            border: "3px solid #008cff",
            padding: "20px",
            paddingTop: "15px",
            marginTop: "20px",
          }}
        >
            {props.children}
            
        </div>

    );

}

export default Sivu;