import { Box, Typography } from '@mui/material';
import { PieChart } from '@mui/x-charts';
import Meno from './Meno';


interface Props {
    tulot: number;
    menot: Meno[];
    menotYhteensa: number;
}

const Piirakka: React.FC<Props> = (props : Props) => {

    const data = [
        ...props.menot.map((meno) => ({
            id: meno.tyyppi,
            value: meno.summa,
            label: meno.tyyppi,
        })),
       
    ];

    return (
        <Box sx={{ 
            textAlign: 'center', 
            marginTop: "20px",
            
            
            }}
        >
            <Typography variant="h6">
                Kytetty budjetti: {((props.menotYhteensa / props.tulot) * 100).toFixed(1)}%
            </Typography>

            {props.tulot >= props.menotYhteensa 
                ?   <Typography variant="h6" color="success">
                        Ylijm:  {(props.tulot - props.menotYhteensa).toFixed(2)}
                    </Typography>
                
                :   <Typography variant="h6" color="error">
                    Alijm:  {(props.menotYhteensa - props.tulot).toFixed(2)}
                    </Typography>
            }

            <PieChart
                sx={{
                    marginTop: "-120px"
                }}
                width={400}
                height={400}
                series={[
                    {
                        data,
                        innerRadius: 20,
                        outerRadius: 100,
                        id: 'pie-series',
                    },
                ]}
            />

            

            

        </Box>
    );
};

export default Piirakka;
